	width: 100%;
	height: 100%;
	background: #ccc;
	position: relative;
	.ant-spin-nested-loading,
	.ant-spin-container {
		height: 100%;
	}
	.tip {
		padding: 10px;
		color: #000;
	}
	.operator {
		position: absolute;
		left: 0;
		top: 0;
		button {
			cursor: pointer;
			padding: 2px 4px;
			margin: 10px;
		}
	}
	.stats-panel {
		position: absolute;
		width: 100%;
		height: 50px;
		bottom: 0;
		right: 0;
		display: flex;
		flex-direction: row;
		justify-content: flex-end;
		& > div {
			margin-left: 20px;
		}
	}
	.date-select {
		position: absolute;
		top: 10px;
		right: 10px;
	}
	.table {
		position: absolute;
		right: 10px;
		top: 50px;
	}

	.three-wrap {
		width: 100%;
		height: 100%;
	}
}
</style>
