import axios from "axios";
import * as THREE from "three";
import { Text, preloadFont } from "troika-three-text";
const labelMap = [
	{ dui_hao: "堆号", chuan_ci: "船次" },
	{ qi_shi: "范围" },
	{ ti_ji: "体积", mi_du: "密度", zhong_liang: "重量" },
	{ re_zhi: "发热量", hui_fen: "灰分" },
	{ shui_fen: "水分", liu_fen: "硫分" },
	{ hui_fa_fen: "挥发分", hui_rong_dian: "灰熔点" },
];

export default {
	data() {
		return {
			basePoints: [],
			allLayoutMeshes: [],
			layoutRecords: [],
		};
	},
	created() {
		preloadFont(
			{
				font: "/fonts/NotoSansSC-Regular.otf",
			},
			() => {}
		);
	},
	methods: {
		async handleLayout(selectDate = "") {
			try {
				const base = this._basePoints;
				if (
					Array.isArray(this.allLayoutMeshes) &&
					this.allLayoutMeshes.length
				)
					this.disposeLayout();
				this.allLayoutMeshes = [];

				const loop = (layoutData, type, index = null) => {
					for (let i = 0, len = layoutData.length; i < len; i++) {
						const item = layoutData[i];
						const sy = item.sy;
						const geo = new THREE.Geometry();

						for (let j = 0, length = sy.length; j < length; j++) {
							const indexs = sy[j].slice(0, 3);
							const curHigh =
								type === "low"
									? sy[j].slice(3, 6)
									: sy[j].slice(6, 9);
							for (let m = 0; m < 3; m++) {
								const baseIndex = indexs[m];
								index != null
									? (base[index][baseIndex].y = curHigh[m])
									: (base[baseIndex].y = curHigh[m]);
								const verticle =
									index != null
										? base[index][baseIndex]
										: base[baseIndex];
								geo.vertices.push(verticle);
							}

							const curPointIndexs = new Array(3)
								.fill(0)
								.map((v, n) => j * 3 + n);

							const face = new THREE.Face3(
								curPointIndexs[0],
								curPointIndexs[1],
								curPointIndexs[2]
							);

							geo.faces.push(face);
						}

						const color = new THREE.Color();
						color.setRGB(item.R, item.G, item.B);
						const material = new THREE.MeshLambertMaterial({
							color,
							side: THREE.DoubleSide,
						});
						geo.computeFaceNormals();

						const mesh = new THREE.Mesh(geo, material);
						this.allLayoutMeshes.push(mesh);
						this.scene.add(mesh);
						this.render();

						if (type === "high") {
							// 创建标注信息
							this.addTextToScene(item, mesh);
						}
					}
				};

				const layoutData = await this.getLatestLayoutData(
					this.selected,
					selectDate
				);

				if (Array.isArray(layoutData) && layoutData.length) {
					for (let i = 0; i < layoutData.length; i++) {
						loop(layoutData[i], "low", i);
						loop(layoutData[i], "high", i);
					}
				} else {
					loop(layoutData, "low");
					loop(layoutData, "high");
				}
			} catch (error) {
				console.log(error);
			}
		},
		addTextToScene(item, parentGeo) {
			const center = this.getGeoCenter(parentGeo);
			for (let i = 0; i < labelMap.length; i++) {
				let str = "";
				for (const key in labelMap[i]) {
					str += labelMap[i][key] + ":" + item[key] + " ";
				}
				const text = new Text();
				text.text = str;
				console.log(str, i);
				this.addTextAttr(text, center, i);
				this.scene.add(text);
			}
			this.render();
		},
		addTextAttr(text, center, lineNo) {
			const lineHeight = 3.2;
			const textAreaHeight = 90;
			const coalWidth = 62;
			text.maxWidth = 80;
			text.color = "black";
			text.anchorX = "left";
			text.anchorY = "top";
			text.lineHeight = lineHeight;
			text.font = "/fonts/NotoSansSC-Regular.otf";
			text.fontSize = 3;
			text.position.x = center.x;
			text.position.y = 0;
			text.rotation.x = -Math.PI / 2;
			if (center.z < 0) {
				// N
				text.position.z = -textAreaHeight + lineNo * lineHeight;
			} else {
				text.position.z = coalWidth + lineNo * lineHeight;
			}
		},
		getGeoCenter(geoIns) {
			geoIns.geometry.computeBoundingBox();
			return geoIns.geometry.boundingBox.getCenter();
		},
		async getLatestLayoutData(coalNum, name = "") {
			return new Promise((resolve, reject) => {
				axios
					.post("/api/modelData")
					.then((res) => {
						return [res.data.json1, res.data.json2];
					})
					.then((result) => {
						Promise.all(result.map((file) => axios.get(file))).then(
							(json) => {
								const jsons = json.map((v) => {
									return v.data;
								});
								console.log(jsons);
								resolve(jsons);
							}
						);
					});

				// resolve([
				// 	require("./data/1号场地STRUCT.json"),
				// 	require("./data/2号场地STRUCT")
				// ]);
			});
		},
		disposeLayout() {
			if (!Array.isArray(this.allLayoutMeshes)) return;
			this.allLayoutMeshes.map((mesh) => {
				this.scene.remove(mesh);
				mesh.geometry.dispose();
				mesh.material.dispose();
			});
			this.allLayoutMeshes = [];
			this.render();
		},
	},
};
